import * as React from "react";
import PropTypes from "prop-types";
import { Link } from "gatsby";
import { Tags } from "@tryghost/helpers-gatsby";
import { readingTime as readingTimeHelper } from "@tryghost/helpers";
import Fire from "@images/icons/FireIcon";

const PostCard = ({ post }) => {
    const url = `/article/${post.slug}/`;
    const readingTime = readingTimeHelper(post);

    return (
        <Link
            to={url}
            className="text-decoration-none border border-dark border-3 p-4"
        >
            <header>
                {post.featured && (
                    <span className="text-danger">
                        <Fire /> Featured
                    </span>
                )}

                <h2 className="mb-4 fw-bold fs-1">{post.title}</h2>

                {post.feature_image && (
                    <div
                        className="post-card-image mb-5"
                        style={{
                            backgroundImage: `url(${post.feature_image})`,
                        }}
                    ></div>
                )}
            </header>

            {/* <section className="post-card-excerpt">{post.excerpt}</section> */}
            <section className="fs-4">
                {post.excerpt.substring(0, 150)}&hellip;
            </section>

            <section className="my-4">
                {post.tags && (
                    <div className="text-muted fs-5">
                        <Tags
                            post={post}
                            visibility="public"
                            autolink={false}
                            classes="px-1 py-1"
                        />
                    </div>
                )}
            </section>

            <footer className="d-flex justify-content-between align-items-center fs-5 text-muted">
                <div className="d-flex align-items-center">
                    <div className="post-card-avatar">
                        <img
                            className={
                                post.primary_author.profile_image
                                    ? "author-profile-image"
                                    : "default-avatar"
                            }
                            src={
                                post.primary_author.profile_image ||
                                "/images/icons/avatar.svg"
                            }
                            alt={post.primary_author.name}
                        />
                    </div>
                    <span>{post.primary_author.name}</span>
                </div>
                <div className="">{readingTime}</div>
            </footer>
        </Link>
    );
};

PostCard.propTypes = {
    post: PropTypes.shape({
        slug: PropTypes.string.isRequired,
        title: PropTypes.string.isRequired,
        feature_image: PropTypes.string,
        featured: PropTypes.bool,
        tags: PropTypes.arrayOf(
            PropTypes.shape({
                name: PropTypes.string,
            })
        ),
        excerpt: PropTypes.string.isRequired,
        primary_author: PropTypes.shape({
            name: PropTypes.string.isRequired,
            profile_image: PropTypes.string,
        }).isRequired,
    }).isRequired,
};

export default PostCard;
